

.container {
    display: flex;
    justify-content: center;
    position: absolute;
    right: 50px
}

/* STEVE */
.steve {
    position: relative;
    width: 50%;
    height: 50%;
    display: flex;
    top: 100px;
    left: 50px;
}
.steveImg {
    position: relative;
    max-height: 200px;
    width: auto;

}

/* OVEJA */
.oveja {
    position: relative;
    -webkit-transform: scaleX(0.25) scaleY(0.25);
    -moz-transform: scaleX(0.25) scaleY(0.25);
    transform: scaleX(0.25) scaleY(0.25);
    filter: FlipH;
    -ms-filter: "FlipH";
    display: flex;
    left: 410px;
}
.ovejaImg{
    position: relative;
    max-width: 100px;

}


